* {
  margin: 0px;
  padding: 0px;
}
.main {
  height: 100vh;
  width: 100%;
  background-color: lightgray;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex-direction: column;
}
.innerDiv {
  height: 90vh;
  width: 40%;
  background-color: whitesmoke;
  margin: 20px 0px;
  box-shadow: 5px 5px 25px -1px rgb(0, 0, 0, 0.5);
  border-radius: 20px;
}
.bottonDiv {
  height: 60vh;
  width: 90%;
  margin-bottom: 20px;
  background-color: whitesmoke;
  box-shadow: 5px 5px 25px -1px rgb(0, 0, 0, 0.5);
  border-radius: 20px;
  display: flex;
  justify-content: space-around;
}
.displayYear {
  height: 95%;
  width: 25%;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 15px;
  box-shadow: 5px 5px 25px -1px rgb(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  text-align: center;
  border: 2px solid gray;
}
#title {
  margin-top: 10px;
  color: rgb(224, 67, 67);
  background: transparent;
  font-size: xx-large;
  font-weight: bold;
  border: 1px solid gray;
  background-color: whitesmoke;
  border-radius: 5px;
  box-shadow: 5px 5px 25px -5px rgb(0, 0, 0, 0.5);
  text-shadow: 1px 3px rgb(0, 0, 0, 0.4);
}

h1 {
  font-size: x-large;
  margin: 15px 0px 5px 0px;
  color: darkgrey;
  background: transparent;
  box-shadow: 5px 5px 25px -5px rgb(0, 0, 0, 0.5);
}
.select {
  width: 350px;
  height: 5vh;
  margin-right: 20px;
  margin-top: 35px;
  margin-bottom: 10px;
  background-color: whitesmoke;
  border-radius: 10px;
  outline: none;
  background: transparent;
  box-shadow: 5px 5px 25px -5px rgb(0, 0, 0, 0.5);
  border: 2px solid gray;
  text-align: center;
  color: rgb(224, 67, 67);
  font-weight: bold;
  font-size: large;
}
select {
  font-size: large;
  font-weight: bolder;
  text-transform: uppercase;
  text-align: center;
}
option {
  font-size: large;
  font-weight: bolder;
  text-transform: uppercase;
}
button {
  width: 100px;
  height: 5vh;
  margin: 20px 20px;
  background-color: whitesmoke;
  border-radius: 10px;
  outline: none;
  border: 2px solid gray;
  color: rgb(224, 67, 67);
  font-size: large;
  font-weight: bold;
  box-shadow: 5px 5px 25px -5px rgb(0, 0, 0, 0.5);
}
button:hover {
  box-shadow: 5px 5px 25px -1px rgb(224, 67, 67, 0.5);
}
.select:hover {
  box-shadow: 5px 5px 25px -1px rgb(224, 67, 67, 0.5);
}
::placeholder {
  color: rgb(224, 67, 67, 0.5);
  text-align: center;
  font-size: large;
  font-weight: bolder;
}
